import React, { useRef } from 'react';
import Angle from './Angle';

interface Props {
  value: number;
  onChange: (value: number, event?: React.SyntheticEvent) => void;
}

const AngleInputBox = ({ value, onChange }: Props) => {
  const ref = useRef<HTMLInputElement>();
  let angle = value;

  function setAngle(value: number) {
    const input = ref.current;
    if (!input) return;
    input.value = String(value);
  }

  function increment() {
    onChange((value + 1) % 360);
  }

  function decrement() {
    const angle = (value - 1) % 360;
    onChange(angle < 0 ? 359 : angle);
  }

  return (
    <div className="inputbox angle">
      <div className="inputbox-angle">
        <Angle
          angle={Number(angle)}
          updateAngle={(value) => {
            onChange && onChange(value);
            setAngle(value);
          }}
          onDragEnd={onChange}
        />
      </div>
      <div className="inputbox-angle-container">
        <button className="inputbox-angle-button" onClick={decrement}>
          <span className="inputbox-angle-decrement">-</span>
        </button>
        <input
          ref={ref as React.MutableRefObject<HTMLInputElement>}
          className="inputbox-angle-input"
          type="text"
          value={angle}
          onChange={(event) => {
            const value = Number(event.target.value);
            if (Number.isNaN(value)) return;
            const formatted = Math.floor(value * 10) / 10;

            setAngle(formatted);
            onChange(formatted, event);
          }}
        />
        <button className="inputbox-angle-button" onClick={increment}>
          <span className="inputbox-angle-increment">+</span>
        </button>
      </div>
    </div>
  );
};

export default AngleInputBox;
