import React, { useEffect, useRef, useState } from 'react';
import { ColorResult } from 'react-color';
import { Pointer, SaturationPointer } from './ColorPickerPointers';
import trash from '../../assets/svg/delete_outline_black_24dp.svg';
import AngleInputBox from './AngleInputBox';
import GradientSlider from './GradientSlider';
import {
  convertToFabricGradient,
  convertToColorStyle,
  matchColorFormat,
  setBackground,
  toGradient,
  toHex,
} from '../../utils/color';
import { fabric } from 'fabric';
import { Saturation, EditableInput, Hue, Alpha } from './CustomColorTools';
import { convertToCoords } from '../../utils/angle';
import { Gradient } from 'fabric/fabric-impl';

interface Style extends React.CSSProperties {}

type Styles = {
  [K in string]: Style | Styles;
};

interface Props {
  fill?: string | fabric.Gradient;
  style: React.CSSProperties;
  placeholder: string;
  updateGradient: (gradient: Gradient) => void;
  redraw: (gradient: Gradient) => void;
  close: () => void;
}

const styles: Styles = {
  editableInput: {
    input: {
      textAlign: 'center',
      width: '100%',
      border: 0,
      outline: 0,
      boxShadow: '#dadada 0px 0px 0px 1px inset',
      padding: '6px 0',
      fontSize: 12,
      marginBottom: 4,
      background: 'transparent',
    },
    label: {
      display: 'block',
      textAlign: 'center',
      fontSize: 11,
      textTransform: 'uppercase',
      color: '#969696',
    },
  },
};

function GradientColorPicker({
  fill,
  style,
  placeholder,
  updateGradient,
  redraw,
  close,
  ...props
}: Props) {
  const sliderRef = useRef<HTMLElement>();

  const [pointer, setPointer] = useState<HTMLElement>();
  const [angle, setAngle] = useState(0);

  let gradient = fill instanceof fabric.Gradient ? fill : toGradient(fill, angle);
  const [selected, setSelected] = useState(gradient.colorStops![0].color);
  const colorText = selected === 'transparent' ? '' : selected;

  useEffect(() => {
    const pointerId = Number(pointer?.dataset.id) || 0;
    if (Number.isNaN(pointerId)) return;

    setSelected(gradient.colorStops![pointerId].color);
  }, [pointer, gradient.colorStops]);

  function onPickerChange(color: ColorResult) {
    const slider = sliderRef.current;
    if (!slider) return;
    const pointerId = Number(pointer?.dataset.id) || 0;

    const formatted = toHex(color.rgb as Extract<ColorResult, 'rgb'>);
    const colorStops = gradient.colorStops!.map((item, i) => {
      if (i === pointerId) {
        return { ...item, color: formatted };
      }
      return item;
    });
    gradient = convertToFabricGradient(colorStops, angle);
    const gradientStyle = convertToColorStyle(colorStops);
    setBackground(slider, gradientStyle);

    updateGradient(gradient);
  }

  function onInputChange(_: ColorResult, event: React.ChangeEvent) {
    const pointerId = Number(pointer?.dataset.id) || 0;
    const value = (event.target as HTMLInputElement).value;
    const colorCode = matchColorFormat(value);

    if (!colorCode) return;

    const colorStops = gradient.colorStops!.map((item, i) => {
      if (i === pointerId) {
        return { ...item, color: colorCode };
      }
      return item;
    });

    gradient = convertToFabricGradient(colorStops, angle);

    updateGradient(gradient);
  }

  function onAngleChange(value: number) {
    setAngle(value);

    gradient = new fabric.Gradient({
      ...gradient,
      coords: convertToCoords(value),
    });
    updateGradient(gradient);
  }

  function removeHandle() {
    const slider = sliderRef.current;
    if (!slider) return;
    const pointerId = Number(pointer?.dataset.id) || 0;

    const colorStops = gradient.colorStops!.filter((_, i) => i !== pointerId);
    if (!colorStops || colorStops.length < 1) {
      close();
      return;
    }
    gradient = convertToFabricGradient(colorStops, angle);
    const gradientStyle = convertToColorStyle(gradient);
    setBackground(slider, gradientStyle);

    updateGradient(gradient);
    setPointer(undefined);
  }

  return (
    <>
      <div className="picker-background" onClick={close}></div>
      <dialog className="picker" open style={style}>
        <section className="picker-saturation">
          <Saturation
            pointer={SaturationPointer}
            color={selected}
            onChange={onPickerChange}
            {...props}
          />
        </section>
        <section className="picker-detail">
          <div className="detail-top">
            <div className="detail-wrapper">
              <div className="detail-swatch" style={{ background: selected }} />
            </div>
            <div className="detail-sliders">
              <div className="detail-hue">
                <Hue pointer={Pointer} color={selected} onChange={onPickerChange} {...props} />
              </div>
              <div className="detail-alpha">
                <Alpha
                  pointer={Pointer}
                  color={selected}
                  onChange={onPickerChange}
                  {...props}
                />
              </div>
            </div>
          </div>
          <div className="detail-bottom">
            {!colorText && <div className="detail-placeholder">{placeholder}</div>}
            <EditableInput
              label="Hex"
              style={
                styles.editableInput as Extract<
                  React.ClassAttributes<typeof EditableInput>,
                  'styles'
                >
              }
              value={colorText}
              onChange={onInputChange}
            />
          </div>
        </section>
        <section>
          <GradientSlider
            sliderRef={sliderRef as React.MutableRefObject<HTMLElement>}
            pointer={pointer}
            value={gradient}
            angle={angle}
            updateGradient={updateGradient}
            changePointer={setPointer}
            redraw={redraw}
          />
          <AngleInputBox value={angle} onChange={onAngleChange} />
        </section>
        <footer className="picker-footer">
          <button className="btn" onClick={removeHandle}>
            <img className="icon" src={trash} alt="remove" />
          </button>
        </footer>
      </dialog>
    </>
  );
}

export default GradientColorPicker;
