import { Rect } from 'fabric/fabric-impl';
import React, { useState } from 'react';
import GradientInputBox from './GradientInputBox';
import { Gradient } from 'fabric/fabric-impl';

interface Props {
  item: Rect;
  renderAll: () => void;
}

const RectController = ({ item, renderAll }: Props) => {
  const [fillColor, setFillColor] = useState(item.fill as string | Gradient | undefined);

  return (
    <>
      <GradientInputBox
        label="채우기 색상"
        name="fillColor"
        color={fillColor}
        updateColor={(gradient) => {
          setFillColor(gradient);
          item.set('fill', gradient);
          renderAll();
        }}
        redraw={(gradient) => {
          item.set('fill', gradient);
          renderAll();
        }}
        onResetClick={() => {
          setFillColor('');
          item.set('fill', undefined);
          renderAll();
        }}
      />
    </>
  );
};

export default RectController;
