import { Canvas, Object } from 'fabric/fabric-impl';
import React from 'react';
import CircleControl from './CircleControl';
import LineControl from './LineControl';
import RectControl from './RectControl';
import TextControl from './TextControl';
import trash from '../../assets/svg/delete_outline_black_24dp.svg';
import duplicate from '../../assets/svg/copy_all_black_24dp.svg';
import BaseControl from './BaseControl';
import { Arrangement, Tool } from '../../hooks/useFabricCanvas';
import GlobalControl from './GlobalControl';

interface Props {
  canvas?: Canvas;
  currentSelected?: Object;
  renderAll: () => void;
  removeSelected: () => void;
  duplicateSelected: () => void;
  arrangeObjectOrder: (object: Object, command: Arrangement) => void;
}

type Control = (props: { item: Object; renderAll: () => void }) => JSX.Element;

type Controls = { [K in Tool]?: Control };

const controls: Controls = {
  circle: CircleControl as Control,
  rect: RectControl as Control,
  text: TextControl as Control,
  line: LineControl as Control,
};

const getControl = (itemType?: Tool) => {
  if (!itemType) return;
  return controls[itemType];
};

const ControlPanel = ({
  canvas,
  currentSelected,
  renderAll,
  removeSelected,
  duplicateSelected,
  arrangeObjectOrder,
}: Props) => {
  const SpecificControl = getControl(currentSelected?.type as Tool);

  return (
    <div className="control-panel">
      {currentSelected ? (
        <>
          <div className="control-panel-btns">
            <button className="btn" onClick={() => removeSelected()}>
              <img className="icon" src={trash} alt="remove" />
            </button>
            <button className="btn" onClick={() => duplicateSelected()}>
              <img className="icon" src={duplicate} alt="duplicate" />
            </button>
          </div>
          <div className="control-panel-container">
            <BaseControl
              item={currentSelected}
              renderAll={renderAll}
              arrangeObjectOrder={arrangeObjectOrder}
            />
            {SpecificControl && (
              <SpecificControl item={currentSelected} renderAll={renderAll} />
            )}
          </div>
        </>
      ) : (
        <GlobalControl canvas={canvas} renderAll={renderAll} />
      )}
    </div>
  );
};

export default ControlPanel;
