import React from 'react';
import { CustomPicker } from 'react-color';
import {
  EditableInput as InputTool,
  Hue as HueTool,
  Alpha as AlphaTool,
  Saturation as SaturationTool,
} from 'react-color/lib/components/common';
import { AlphaProps } from 'react-color/lib/components/common/Alpha';
import { EditableInputProps } from 'react-color/lib/components/common/EditableInput';
import { HueProps } from 'react-color/lib/components/common/Hue';
import { SaturationProps } from 'react-color/lib/components/common/Saturation';

interface HSLColor {
  h: number;
  s: number;
  l: number;
  a: number;
}

export const EditableInput = CustomPicker((props: EditableInputProps) => (
  <InputTool {...props} />
));

export const Saturation = CustomPicker((props: SaturationProps) => (
  <SaturationTool {...props} />
));

export const Hue = CustomPicker((props: HueProps & { hsl?: HSLColor }) => (
  <HueTool {...props} />
));

export const Alpha = CustomPicker((props: AlphaProps) => <AlphaTool {...props} />);
