import React, { useState } from 'react';
import { Text, Gradient } from 'fabric/fabric-impl';
import GradientInputBox from './GradientInputBox';
import SelectBox from './SelectBox';
import { fonts, loadFontFamily } from '../../utils/font-loader';

interface Props {
  item: Text;
  renderAll: () => void;
}

const TextControl = ({ item, renderAll }: Props) => {
  const [fillColor, setFillColor] = useState(item.fill as string | Gradient | undefined);
  const [fontFamily, setFontFamily] = useState(item.get('fontFamily'));

  return (
    <>
      <SelectBox
        label="폰트"
        value={fontFamily}
        options={fonts}
        onChange={(event: React.ChangeEvent) => {
          const value = (event.target as HTMLInputElement).value;
          if (value === 'Times New Roman') {
            item.set('fontFamily', value);
            renderAll();
          } else {
            loadFontFamily(value)
              .then(() => {
                item.set('fontFamily', value);
                renderAll();
              })
              .catch(console.log);
          }
          setFontFamily(value);
        }}
      />
      <GradientInputBox
        label="채우기 색상"
        name="fillColor"
        color={fillColor}
        updateColor={(gradient) => {
          setFillColor(gradient);
          item.set('fill', gradient);
          renderAll();
        }}
        redraw={(gradient) => {
          item.set('fill', gradient);
          renderAll();
        }}
        onResetClick={() => {
          setFillColor('');
          item.set('fill', undefined);
          renderAll();
        }}
      />
    </>
  );
};

export default TextControl;
