import { fabric } from 'fabric';
import { IObjectOptions } from 'fabric/fabric-impl';

interface Point {
  x: number;
  y: number;
}

interface Options extends IObjectOptions {
  centerX?: number;
  centerY?: number;
  cropLeft?: number;
  cropRight?: number;
  waveLength?: number;
  amplitude?: number;
  filledY?: number;
  top?: number;
  left?: number;
}

export type CurvedLineType = typeof CurvedLine;

const CurvedLine = fabric.util.createClass(fabric.Object, {
  type: 'line',
  initialize: function (start: Point, end: Point, options?: Options) {
    this.set('startPoint', start || { x: 0, y: 100 });
    this.set('endPoint', end || { x: 0, y: 100 });
    const startPoint = this.get('startPoint');
    const endPoint = this.get('endPoint');
    const amplitude = options?.amplitude || 0;
    const width = endPoint.x - startPoint.x;
    const height = amplitude * 2;

    this.callSuper('initialize', {
      objectCaching: false,
      width,
      height,
      originX: 'center',
      originY: 'center',
      fill: undefined,
      stroke: this.get('stroke') || '#000',
      top: (options?.top || height) - height / 2,
      left: options?.left || width,
      centerX: options?.centerX || 0,
      centerY: options?.centerY || 0,
      waveLength: options?.waveLength || Math.floor(width / 4),
      amplitude,
      cropLeft: options?.cropLeft || 0,
      cropRight: options?.cropRight || 0,
      filledY: options?.filledY || this.get('top'),
      strokeStyle: this.get('strokeStyle') || 'black',
      ...options,
    });
  },
  toString: function () {
    return `${this.x1}/${this.y}, ${this.x2}/${this.y2}`;
  },
  toObject: function () {
    return fabric.util.object.extend(this.callSuper('toObject'), {
      centerX: this.get('centerX'),
      centerY: this.get('centerY'),
      waveLength: this.get('waveLength'),
      amplitude: this.get('amplitude'),
      cropLeft: this.get('cropLeft'),
      cropRight: this.get('cropRight'),
      filledY: this.get('filledY'),
      startPoint: this.get('startPoint'),
      endPoint: this.get('endPoint'),
    });
  },
  _render: function (ctx: CanvasRenderingContext2D) {
    this.callSuper('_render', ctx);
    const centerX = this.get('centerX');
    const centerY = this.get('centerY');
    const amplitude = this.get('amplitude');
    const waveLength = this.get('waveLength');
    const cropLeft = this.get('cropLeft');
    const cropRight = this.get('cropRight');
    const fill = this.get('fill');
    const filledY = this.get('filledY');

    const canvasWidth = this.canvas.width;
    const absAmplitude = Math.abs(amplitude);
    const paddingRange = 20;

    if (absAmplitude < paddingRange) {
      this.set('padding', paddingRange - absAmplitude);
    } else {
      this.set('padding', 0);
    }

    ctx.lineWidth = this.get('strokeWidth');
    ctx.strokeStyle = this.get('stroke');
    ctx.setLineDash(this.get('strokeDashArray') || [0]);

    ctx.beginPath();
    const start = cropLeft - canvasWidth / 2;
    const end = canvasWidth / 2 - cropRight;
    for (let i = start; i < end; i++) {
      ctx.lineTo(i, centerY - Math.sin((i - centerX) * (Math.PI / waveLength)) * amplitude);
    }

    this.set({ height: absAmplitude * 2 });

    if (fill) {
      ctx.lineTo(end, filledY);
      ctx.lineTo(start, filledY);
      ctx.closePath();

      ctx.fillStyle = fill || undefined;
      if (fill instanceof fabric.Gradient) {
        const width = this.get('width');
        const height = this.get('height');
        const coords = Object.values(fill.coords!).map((coord, i) => {
          const newCoord = coord * 2 - 1;
          if (i % 2) {
            return newCoord * width;
          }
          return newCoord * height;
        });
        const colors = fill.colorStops!;
        const gradient = ctx.createLinearGradient(coords[0], coords[1], coords[2], coords[3]);

        colors.forEach(({ offset, color }) => {
          gradient.addColorStop(offset, color);
        });

        ctx.fillStyle = gradient;
      }
      ctx.fill();
    }

    ctx.stroke();
  },
});

export default CurvedLine;
