function calcLengthByAngle(angle: number) {
  return Math.floor(Math.tan((angle * Math.PI) / 180) * 100) / 100;
}

export function convertToCoords(angle: number) {
  const conversion = angle % 90;
  if (angle < 90) {
    if (angle < 45) {
      // 45도
      return { x1: 0, y1: 0, x2: 1, y2: calcLengthByAngle(conversion) };
    } else {
      // 90도
      return { x1: 0, y1: 0, x2: 1 - calcLengthByAngle(conversion % 45), y2: 1 };
    }
  } else if (angle < 180) {
    if (conversion < 45) {
      // 135도
      return { x1: 1, y1: 0, x2: 1 - calcLengthByAngle(conversion), y2: 1 };
    } else {
      // 180도
      return { x1: 1, y1: 0, x2: 0, y2: 1 - calcLengthByAngle(conversion % 45) };
    }
  } else if (angle < 270) {
    if (conversion < 45) {
      // 225도
      return { x1: 1, y1: 1, x2: 0, y2: 1 - calcLengthByAngle(conversion) };
    } else {
      // 270도
      return { x1: 1, y1: 1, x2: calcLengthByAngle(conversion % 45), y2: 0 };
    }
  } else if (angle < 360) {
    if (conversion < 45) {
      // 315도
      return { x1: 0, y1: 1, x2: calcLengthByAngle(conversion), y2: 0 };
    } else {
      // 360도
      return { x1: 0, y1: 1, x2: 1, y2: calcLengthByAngle(conversion % 45) };
    }
  }
}
