import { Object } from 'fabric/fabric-impl';
import React, { useState } from 'react';
import InputBox from './InputBox';
import SelectBox from './SelectBox';
import SelectButtons from './SelectButtons';
import align_center from '../../assets/svg/align_horizontal_center_black_24dp.svg';
import align_right from '../../assets/svg/align_horizontal_right_black_24dp.svg';
import align_left from '../../assets/svg/align_horizontal_left_black_24dp.svg';
import send_to_back from '../../assets/svg/send-to-back.svg';
import send_backward from '../../assets/svg/send-backward.svg';
import bring_forward from '../../assets/svg/bring-forward.svg';
import bring_to_front from '../../assets/svg/bring-to-front.svg';
import InputContainer from './InputContainer';
import { matchColorFormat } from '../../utils/color';
import {
  StrokeType,
  strokeTypes,
  toStrokeDashArray,
  toStrokeType,
} from '../../utils/stroke-type-converter';
import { Arrangement } from '../../hooks/useFabricCanvas';
import ColorInputBox from './ColorInputBox';

interface Props {
  item: Object;
  renderAll: () => void;
  arrangeObjectOrder: (object: Object, command: Arrangement) => void;
}

const BaseControl = ({ item, renderAll, arrangeObjectOrder }: Props) => {
  const initialStrokeColor = matchColorFormat(item.stroke as string) || '';

  const [opacity, setOpacity] = useState(item.opacity ? item.opacity * 100 : 1);
  const [skewX, setSkewX] = useState(item.skewX || 0);
  const [skewY, setSkewY] = useState(item.skewY || 0);
  const [strokeWidth, setStrokeWidth] = useState(item.strokeWidth);
  const [strokeType, setStrokeType] = useState(toStrokeType(item.get('strokeDashArray')));
  const [strokeColor, setStrokeColor] = useState(initialStrokeColor);

  const alignments = [
    {
      content: <img className="icon" src={align_left} alt="왼쪽 정렬" />,
      onClick: () => {
        const { height } = item.canvas!;
        const itemWidth = item.getBoundingRect().width;
        item.set({ left: 0 + itemWidth / 2, top: height! / 2 });
        renderAll();
      },
    },
    {
      content: <img className="icon" src={align_center} alt="가운데 정렬" />,
      onClick: () => {
        const { width, height } = item.canvas!;
        item.set({ left: width! / 2, top: height! / 2 });
        renderAll();
      },
    },
    {
      content: <img className="icon" src={align_right} alt="오른쪽 정렬" />,
      onClick: () => {
        const { width, height } = item.canvas!;
        const itemWidth = item.getBoundingRect().width;
        item.set({ left: width! - itemWidth / 2, top: height! / 2 });
        renderAll();
      },
    },
  ];

  const arrangements = [
    {
      content: <img className="icon" src={send_to_back} alt="맨 뒤로" />,
      onClick: () => {
        arrangeObjectOrder(item, 'back');
        renderAll();
      },
    },
    {
      content: <img className="icon" src={send_backward} alt="뒤로" />,
      onClick: () => {
        arrangeObjectOrder(item, 'backward');
        renderAll();
      },
    },
    {
      content: <img className="icon" src={bring_forward} alt="앞으로" />,
      onClick: () => {
        arrangeObjectOrder(item, 'forward');
        renderAll();
      },
    },
    {
      content: <img className="icon" src={bring_to_front} alt="맨 앞으로" />,
      onClick: () => {
        arrangeObjectOrder(item, 'front');
        renderAll();
      },
    },
  ];

  return (
    <>
      <SelectButtons label="정렬" options={alignments} />
      <SelectButtons label="순서" options={arrangements} />
      <InputBox
        label="투명도"
        className="control-panel-input"
        name="opacity"
        type="range"
        value={opacity}
        min={0}
        max={100}
        onChange={(event: React.ChangeEvent) => {
          const value = Number((event.target as HTMLInputElement).value);
          item.set('opacity', value / 100);
          setOpacity(value);
          renderAll();
        }}
      />
      <InputBox
        label="기울임(가로)"
        className="control-panel-input"
        name="skewX"
        type="range"
        value={skewX}
        min={-50}
        max={50}
        onChange={(event: React.ChangeEvent) => {
          const value = Number((event.target as HTMLInputElement).value);
          item.set('skewX', value);
          setSkewX(value);
          renderAll();
        }}
      />
      <InputBox
        label="기울임(세로)"
        className="control-panel-input"
        name="skewY"
        type="range"
        value={skewY}
        min={-50}
        max={50}
        onChange={(event: React.ChangeEvent) => {
          const value = Number((event.target as HTMLInputElement).value);
          item.set('skewY', value);
          setSkewY(value);
          renderAll();
        }}
      />
      {
        <InputContainer
          Super={
            <ColorInputBox
              label="선 색상"
              name="fillColor"
              value={strokeColor}
              onInputChange={(value, color) => {
                setStrokeColor(value);

                if (color) {
                  item.set('stroke', color);
                } else if (!value) {
                  item.set('stroke', 'transparent');
                }
                renderAll();
              }}
              onPickerChange={(color) => {
                setStrokeColor(color);

                item.set('stroke', color);
                renderAll();
              }}
              onResetClick={() => {
                setStrokeColor('');
                item.set('stroke', 'transparent');
                renderAll();
              }}
            />
          }
          condition={Boolean(strokeColor)}
          Sub={
            <>
              <SelectBox
                label="선 종류"
                name="strokeDashArray"
                options={strokeTypes}
                value={strokeType}
                onChange={(event: React.ChangeEvent) => {
                  const value = (event.target as HTMLSelectElement).value;
                  const strokeDashArray = toStrokeDashArray(value as StrokeType);
                  item.set('strokeDashArray', strokeDashArray);
                  setStrokeType(value);
                  renderAll();
                }}
              />
              <InputBox
                label="선 두께"
                className="control-panel-input"
                name="strokeWidth"
                type="range"
                value={strokeWidth}
                min={0}
                max={10}
                step={0.1}
                valueStep={0.1}
                onChange={(event: React.ChangeEvent) => {
                  const value = Number((event.target as HTMLInputElement).value);
                  item.set('strokeWidth', value);
                  setStrokeWidth(value);
                  renderAll();
                }}
              />
            </>
          }
        />
      }
    </>
  );
};

export default BaseControl;
