export type StrokeType = 'line' | 'dashed-1' | 'dashed-2';

export const strokeTypes = [
  {
    value: 'line',
    name: '실선',
  },
  {
    value: 'dashed-1',
    name: '점선',
  },
  {
    value: 'dashed-2',
    name: '긴 점선',
  },
];

export function toStrokeDashArray(value: StrokeType) {
  switch (value) {
    case 'line':
      return undefined;
    case 'dashed-1':
      return [3, 5];
    case 'dashed-2':
      return [10, 10];
    default:
      return undefined;
  }
}

export function toStrokeType(value: number[] | undefined) {
  switch (JSON.stringify(value)) {
    case 'undefined':
      return 'line';
    case '[3,5]':
      return 'dashed-1';
    case '[10,10]':
      return 'dashed-2';
    default:
      return 'line';
  }
}
