import React from 'react';

interface Option {
  value: string;
  name: string;
  group?: string;
}

interface Props extends React.SelectHTMLAttributes<HTMLSelectElement> {
  label: string;
  value?: string;
  onChange: (event: React.ChangeEvent) => void;
  options?: Option[];
}

const SelectBox = ({ label, value, onChange, options, ...props }: Props) => {
  const id = Math.floor(Date.now() * Math.random() * Math.pow(10, 3)).toString(16);

  const groups = options?.reduce((result, option) => {
    const copied = [...result];

    if (!option.group) return copied;
    const groupIndex = copied.findIndex(
      (o) => Array.isArray(o) && option.group === (o[0] as Option).group
    );

    if (groupIndex > -1) {
      (copied[groupIndex] as Option[]).push(option);
    } else {
      copied.push([option]);
    }

    return copied;
  }, new Array(0));

  const renderOptions = (options?: Option[]) =>
    options?.map(({ value, name }) => (
      <option key={value} value={value}>
        {name}
      </option>
    ));

  const renderGroups = (groups?: any[]) => {
    const groupsComponents = groups?.map((group) => {
      const groupname = group[0].group;
      return (
        <optgroup key={groupname} label={groupname}>
          {renderOptions(group)}
        </optgroup>
      );
    });

    if (Array.isArray(groupsComponents) && groupsComponents.length > 0) {
      return groupsComponents;
    }

    return undefined;
  };

  return (
    <div className={`inputbox select`}>
      <label className="inputbox-label" htmlFor={id}>
        {label}
      </label>
      <select className="inputbox-select" id={id} value={value} onChange={onChange} {...props}>
        {renderGroups(groups) || renderOptions(options)};
      </select>
    </div>
  );
};

export default SelectBox;
