function addTransparentElement() {
  const dragImageElement = document.createElement('div');
  dragImageElement.style.opacity = '0';
  document.body.appendChild(dragImageElement);
  return dragImageElement;
}

export function hideDragImage(event: React.DragEvent) {
  const dragImage = addTransparentElement();
  event.dataTransfer.setDragImage(dragImage, 0, 0);
  return dragImage;
}

export function removeDragImage(dragImage?: HTMLElement) {
  if (!dragImage) return;
  document.body.removeChild(dragImage);
}
