import { fabric } from 'fabric';
import { Gradient } from 'fabric/fabric-impl';
import { convertToCoords } from './angle';

export interface Color {
  offset: number;
  color: string;
}

export function setBackground(element: HTMLElement, gradientStyle: string) {
  element.style.background = gradientStyle;
}

export function limitGradientOffset(offset: number) {
  if (offset < 0) {
    return 0;
  } else if (offset > 1) {
    return 1;
  }
  return offset;
}

function getGradientStyle(color: Color[]) {
  const colors = color.map(({ offset, color }) => {
    const percentage = offset * 100;
    return `${color} ${percentage}%`;
  });
  return `linear-gradient(90deg, ${colors})`;
}

export function convertToColorStyle(color?: string | Gradient | Color[]) {
  let style = '';
  if (color instanceof fabric.Gradient) {
    const colorStops = color.colorStops!;
    const length = colorStops.length;
    style = length > 1 ? getGradientStyle(colorStops) : colorStops[0].color;
  } else if (Array.isArray(color)) {
    const length = color.length;
    style = length > 1 ? getGradientStyle(color) : color[0].color;
  } else if (color) {
    style = color;
  }
  return style;
}

export function matchColorFormat(value?: string) {
  if (!value) return null;

  const matches = value.match(/\B#[0-9a-f]{8}|\B#[0-9a-f]{6}|\B#[0-9a-f]{3}/i);

  if (matches === null) {
    return null;
  } else {
    const match = matches[0];
    let colorCode = match;

    if (colorCode.length < 5) {
      colorCode = Array.from(colorCode.slice(1)).reduce((result: string, char: string) => {
        return result + char.repeat(2);
      }, '#');
    }

    return colorCode;
  }
}

export function convertToFabricGradient(colors: Color[], angle: number) {
  const coords = convertToCoords(angle);

  return new fabric.Gradient({
    type: 'linear',
    colorStops: colors,
    gradientUnits: 'percentage',
    coords: coords,
  });
}

export function toHex(rgba: { r: number; g: number; b: number; a: number }) {
  const { r, g, b, a } = rgba;
  const colorString = `rgba(${r}, ${g}, ${b}, ${a})`;
  return '#' + fabric.Color.fromRgba(colorString).toHexa();
}

export function toTwoDigitsHex(value: number) {
  const hex = Math.floor((value / 100) * 255).toString(16);
  const twoDigitsHex = hex.length < 2 ? '0' + hex : hex;
  return twoDigitsHex;
}

export function toFillColor(fillColor: any) {
  return typeof fillColor === 'string' ? fillColor : '';
}

export function toGradient(fillColor: string | undefined, angle: number): fabric.Gradient {
  const colorStops = [{ offset: 0, color: fillColor || 'transparent' }];
  return convertToFabricGradient(colorStops, angle);
}
