import React, { useState } from 'react';
import { ColorResult } from 'react-color';
import remove from '../../assets/svg/close_black_24dp.svg';
import { matchColorFormat, toHex } from '../../utils/color';
import ColorPicker from './ColorPicker';

interface Props extends React.InputHTMLAttributes<HTMLInputElement> {
  label?: string;
  className?: string;
  name?: string;
  value: string;
  onInputChange: (value: string, color: string | null, event: React.ChangeEvent) => void;
  onPickerChange: (color: string, event: React.SyntheticEvent) => void;
  onResetClick: (event: React.MouseEvent) => void;
}

const ColorInputBox = ({
  label,
  name,
  value,
  onInputChange,
  onResetClick,
  onPickerChange,
}: Props) => {
  const [isOpenPicker, setIsOpenPicker] = useState(false);
  const [pickerStyle, setPickerStyle] = useState<any>();

  const id = Math.floor(Date.now() * Math.random() * Math.pow(10, 3)).toString(16);
  return (
    <div className="inputbox color">
      <label className="inputbox-label" htmlFor={id}>
        {label}
      </label>
      <div className="inputbox-wrapper">
        <input
          id={id}
          className="inputbox-input color"
          name={name}
          type="text"
          value={value}
          placeholder="색상 없음"
          onChange={(event) => {
            const value = (event.target as HTMLInputElement).value.trim();
            const colorCode = matchColorFormat(value);
            onInputChange && onInputChange(value, colorCode, event);
          }}
        />
        {value && (
          <button className="reset" onClick={onResetClick}>
            <img className="icon" src={remove} alt="되돌리기" />
          </button>
        )}
      </div>
      <div className="inputbox-sub color">
        <div
          className="inputbox-color"
          style={{ background: value }}
          onClick={(event: React.MouseEvent) => {
            const { clientX, clientY } = event;
            const { height } = document.body.getBoundingClientRect();

            const y = Math.floor((clientY / height) * 100) < 80 ? -50 : -100;
            const pickerStyle = {
              transform: `translate(5%,${y}%)`,
              top: clientY,
              left: clientX,
            };

            setPickerStyle(pickerStyle);
            !isOpenPicker && setIsOpenPicker(true);
          }}
        ></div>
        {isOpenPicker && (
          <ColorPicker
            style={pickerStyle}
            color={value}
            placeholder="색상 없음"
            onChange={(color: ColorResult, event: React.ChangeEvent) => {
              let formatted = toHex(color.rgb as Extract<ColorResult, 'rgb'>);
              onPickerChange(formatted, event);
            }}
            onInputChange={(_, event) => {
              const value = (event.target as HTMLInputElement).value;
              const colorCode = matchColorFormat(value);
              onInputChange && onInputChange(value, colorCode, event);
            }}
            close={() => setIsOpenPicker(false)}
          />
        )}
      </div>
    </div>
  );
};

export default ColorInputBox;
