import React, { useState } from 'react';
import { CurvedLineType } from '../../library/fabric/curvedLine';
import { Gradient } from 'fabric/fabric-impl';
import InputContainer from './InputContainer';
import InputBox from './InputBox';
import GradientInputBox from './GradientInputBox';

interface Props {
  item: CurvedLineType;
  renderAll: () => void;
}

const LineControl = ({ item, renderAll }: Props) => {
  const { canvas } = item;

  const [amplitude, setAmplitude] = useState(item.amplitude);
  const [waveLength, setWaveLength] = useState(item.waveLength);
  const [fillColor, setFillColor] = useState(item.fill as string | Gradient | undefined);
  const [centerX, setCenterX] = useState(item.centerX);
  const [cropLeft, setCropLeft] = useState(item.cropLeft);
  const [cropRight, setCropRight] = useState(item.cropRight);
  const [filledY, setFilledY] = useState(item.filledY || 0);

  return (
    <>
      <InputContainer
        Super={
          <InputBox
            label="진폭"
            className="controller-input"
            name="amplitude"
            type="range"
            value={amplitude}
            min={-(canvas.height / 2)}
            max={canvas.height / 2}
            onChange={(event: React.ChangeEvent) => {
              const { value } = event.target as HTMLInputElement;
              item.set('amplitude', value);
              setAmplitude(value);
              renderAll();
            }}
          />
        }
        condition={Number(amplitude) !== 0}
        Sub={
          <>
            <InputBox
              label="파장"
              className="controller-input"
              name="waveLength"
              type="range"
              value={waveLength}
              min={2}
              max={canvas.width * 2}
              step={10}
              onChange={(event: React.ChangeEvent) => {
                const { value } = event.target as HTMLInputElement;
                item.set('waveLength', value);
                setWaveLength(value);
                renderAll();
              }}
            />
            <InputBox
              label="가로 이동"
              className="controller-input"
              name="centerX"
              type="range"
              value={centerX}
              min={0}
              max={Math.floor(waveLength * 2.5)}
              onChange={(event: React.ChangeEvent) => {
                const { value } = event.target as HTMLInputElement;
                item.set('centerX', value);
                setCenterX(value);
                renderAll();
              }}
            />
          </>
        }
      />
      <InputBox
        label="왼쪽 범위"
        className="controller-input"
        name="cropLeft"
        type="range"
        value={cropLeft}
        min={0}
        max={canvas.width - cropRight}
        onChange={(event: React.ChangeEvent) => {
          const { value } = event.target as HTMLInputElement;
          item.set('cropLeft', value);
          setCropLeft(value);
          renderAll();
        }}
      />
      <InputBox
        label="오른쪽 범위"
        className="reverse"
        name="cropRight"
        type="range"
        value={cropRight}
        min={0}
        max={canvas.width - cropLeft}
        onChange={(event: React.ChangeEvent) => {
          const { value } = event.target as HTMLInputElement;
          item.set('cropRight', value);
          setCropRight(value);
          renderAll();
        }}
      />
      {
        <InputContainer
          Super={
            <GradientInputBox
              label="채우기 색상"
              name="fillColor"
              color={fillColor}
              updateColor={(gradient) => {
                setFillColor(gradient);
                item.set('fill', gradient);
                renderAll();
              }}
              redraw={(gradient) => {
                item.set('fill', gradient);
                renderAll();
              }}
              onResetClick={() => {
                setFillColor('');
                item.set('fill', undefined);
                renderAll();
              }}
            />
          }
          condition={Boolean(fillColor)}
          Sub={
            <>
              <InputBox
                label="채우기 범위"
                className="controller-input"
                name="filledY"
                type="range"
                value={filledY}
                min={-canvas.height}
                max={canvas.height}
                onChange={(event: React.ChangeEvent) => {
                  const { value } = event.target as HTMLInputElement;
                  item.set('filledY', value);
                  setFilledY(value);
                  renderAll();
                }}
              />
            </>
          }
        />
      }
    </>
  );
};

export default LineControl;
