import React from 'react';
import { hideDragImage, removeDragImage } from '../../utils/drag-image';

interface Props {
  angle: number;
  updateAngle: (angle: number) => void;
  onDragEnd: (angle: number, event: React.DragEvent) => void;
}

function preventResetLastCoords() {
  // preventDefault를 해주지 않으면 drag 이벤트의 마지막 좌표값이 0이 됨
  document.addEventListener('dragover', (event) => event.preventDefault(), { once: true });
}

const SENSITIVITY = 0.1;

const Angle = ({ angle, updateAngle, onDragEnd }: Props) => {
  let dragImage: HTMLElement | undefined;
  let updatable = true;

  function onDragStart(event: React.DragEvent) {
    dragImage = hideDragImage(event);
  }

  function onDrag(event: React.DragEvent) {
    const pointer = event.currentTarget as HTMLElement;

    const { top, height } = pointer.getBoundingClientRect();
    const y = event.clientY;

    if (!updatable) return;
    updatable = false;
    requestAnimationFrame(() => {
      const center = top + height / 2;
      const changed = angle + (center - y) * SENSITIVITY;
      const formatted = Math.floor(changed) % 360;
      const repeated = formatted < 0 ? 360 + formatted : formatted;

      pointer.style.transform = `rotate(${repeated}deg)`;
      pointer.dataset.angle = String(repeated);
      updateAngle(repeated);

      updatable = true;
    });

    preventResetLastCoords();
  }

  function _onDragEnd(event: React.DragEvent) {
    const pointer = event.currentTarget as HTMLElement;
    onDragEnd(Number(pointer.dataset.angle), event);
    removeDragImage(dragImage);
  }

  return (
    <div className="angle">
      <button
        className="angle-pointer"
        draggable
        onDragStart={onDragStart}
        onDrag={onDrag}
        onDragEnd={_onDragEnd}
        data-angle={angle}
        style={{ transform: `rotate(${angle}deg)` }}
      >
        <div className="angle-circle"></div>
      </button>
    </div>
  );
};

export default Angle;
