import { useState } from 'react';
import { fabric } from 'fabric';
import {
  Canvas,
  IEvent,
  IImageOptions,
  Image as FabImage,
  IRectOptions,
  Object as FabObject,
} from 'fabric/fabric-impl';
import CurevedLine from '../library/fabric/curvedLine';
import { getUplodedImageData } from '../utils/file-transfer';
import Cropper from '../library/fabric/cropper';

interface Point {
  x: number;
  y: number;
}

export type Tool = 'circle' | 'rect' | 'text' | 'line' | 'select' | 'image';

export type Arrangement = 'back' | 'backward' | 'forward' | 'front';

const useFabricCanvas = (width: number, height: number) => {
  const [canvas, setCanvas] = useState<Canvas>();
  const [tool, setTool] = useState<Tool>('select');
  const [currentSelected, setCurrentSelected] = useState<FabObject>();

  function init(canvasElement: HTMLCanvasElement) {
    if (!canvasElement) return;
    setCanvas(
      new fabric.Canvas(canvasElement, { width, height, preserveObjectStacking: true })
    );
  }

  function renderAll() {
    canvas?.renderAll();
  }

  function changeCurrentSelected(item?: FabObject) {
    setCurrentSelected(item);
  }

  function changeTool(tool: Tool) {
    setTool(tool);
    renderAll();
  }

  function selectObject(object: FabObject) {
    canvas?.setActiveObject(object);
  }

  function addRect(point?: Point, options?: IRectOptions) {
    const rect = new fabric.Rect({
      originX: 'center',
      originY: 'center',
      width: 100,
      height: 100,
      stroke: '#000',
      fill: undefined,
      left: point?.x,
      top: point?.y,
      ...options,
    });
    rect.set('type', 'rect');
    canvas?.add(rect);
    return rect;
  }

  function addLine(point?: Point) {
    const line = new CurevedLine(
      { x: 0, y: height / 2 },
      { x: width, y: height / 2 },
      { left: point?.x, top: point?.y }
    );

    line.set('type', 'line');
    canvas?.add(line);
    return line;
  }

  function addCircle(point?: Point) {
    const circle = new fabric.Circle({
      originX: 'center',
      originY: 'center',
      radius: 50,
      stroke: '#000',
      fill: undefined,
      left: point?.x,
      top: point?.y,
    });
    circle.set('type', 'circle');
    canvas?.add(circle);
    return circle;
  }

  function addText(point?: Point) {
    const text = new fabric.IText('텍스트를 입력하세요.', {
      editable: true,
      fontFamily: 'Times New Roman',
      originX: 'center',
      originY: 'center',
      left: point?.x,
      top: point?.y,
      fill: '#000',
    });
    text.set('type', 'text');
    canvas?.add(text);
    return text;
  }

  function addImage(
    imageURL: string,
    point?: Point,
    options?: IImageOptions,
    callback?: (image: FabImage) => void
  ) {
    fabric.Image.fromURL(
      imageURL,
      (image) => {
        image.set('type', 'image');
        canvas?.add(image);
        if (callback) callback(image);
      },
      {
        originX: 'center',
        originY: 'center',
        left: point?.x,
        top: point?.y,
        ...options,
      }
    );
  }

  function addCropper() {
    const cropper = new Cropper(width, height);

    canvas?.add(cropper);
    selectObject(cropper);
    return cropper;
  }

  function observer(event: string, callback: (options: IEvent) => void) {
    canvas?.on(event, callback);
  }

  function removeObserver(event: string) {
    canvas?.off(event);
  }

  function getImage(imageURL: string, callback: (image: FabImage) => void) {
    fabric.Image.fromURL(imageURL, (image) => callback(image));
  }

  function drawItem(point?: Point, payload?: any) {
    switch (tool) {
      case 'circle':
        selectObject(addCircle(point));
        break;
      case 'rect':
        selectObject(addRect(point));
        break;
      case 'text':
        selectObject(addText(point));
        break;
      case 'line':
        selectObject(addLine(point));
        break;
      case 'image':
        addImage(
          payload,
          point,
          {
            left: point?.x,
            top: point?.y,
            scaleX: 0.5,
            scaleY: 0.5,
            opacity: 1,
          },
          selectObject
        );
        break;
      case 'select':
        break;
      default:
        throw new Error(`invalid tool: ${tool}`);
    }
  }

  function toDataURL() {
    return canvas?.toDataURL() || '';
  }

  function removeSelected() {
    const selected = canvas?.getActiveObject();

    if (!selected) return;
    canvas?.remove(selected);
  }

  function duplicateSelected() {
    const selected = canvas?.getActiveObject();
    if (!selected) return;

    if (selected?.type === 'line') {
      // custom
      const options = selected.toObject();
      const line = new CurevedLine(
        { x: 0, y: height / 2 },
        { x: width, y: height / 2 },
        { ...options, left: options.left + 20, top: options.top + 20 }
      );

      line.set('type', 'line');
      canvas?.add(line);
      selectObject(line);
    } else {
      selected.clone((instance: FabObject) => {
        instance.set('left', instance.left! + 20);
        instance.set('top', instance.top! + 20);
        canvas?.add(instance);
        selectObject(instance);
      });
    }
  }

  function arrangeObjectOrder(object: FabObject, command: Arrangement) {
    switch (command) {
      case 'back':
        canvas?.sendToBack(object);
        break;
      case 'backward':
        canvas?.sendBackwards(object);
        break;
      case 'forward':
        canvas?.bringForward(object);
        break;
      case 'front':
        canvas?.bringToFront(object);
        break;
      default:
        throw new Error(`invalid order`);
    }
  }

  return {
    canvas,
    tool,
    currentSelected,
    init,
    renderAll,
    drawItem,
    changeTool,
    changeCurrentSelected,
    observer,
    removeObserver,
    removeSelected,
    duplicateSelected,
    toDataURL,
    addImage,
    addRect,
    selectObject,
    addCropper,
    getImage,
    getUplodedImageData,
    arrangeObjectOrder,
  };
};

export default useFabricCanvas;
