import React from 'react';

interface Props extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  color?: string;
  onClick: (event: React.MouseEvent) => void;
}

const GradientColorHandle = ({
  id,
  color,
  style,
  onDragStart,
  onDragEnd,
  onClick,
  ...props
}: Props) => {
  return (
    <>
      <button
        className="gradient-color"
        style={style}
        draggable
        onClick={onClick}
        onDragStart={onDragStart}
        onDragEnd={onDragEnd}
        data-id={id}
        {...props}
      >
        <div className="gradient-frame"></div>
      </button>
    </>
  );
};

export default GradientColorHandle;
