import React, { useState } from 'react';
import { Canvas } from 'fabric/fabric-impl';
import ColorInputBox from './ColorInputBox';

interface Props {
  canvas?: Canvas;
  renderAll: () => void;
}

const GlobalControl = ({ canvas, renderAll }: Props) => {
  const initialFill = canvas?.fill ? String(canvas?.fill) : '';
  const [fillColor, setFillColor] = useState(initialFill);

  return (
    <>
      <ColorInputBox
        label="배경 색상"
        name="fillColor"
        value={fillColor}
        onInputChange={(value, color) => {
          if (!canvas) return;
          setFillColor(value);

          if (color) {
            canvas.backgroundColor = color;
          } else if (!value) {
            canvas.backgroundColor = undefined;
          }
          renderAll();
        }}
        onPickerChange={(color) => {
          if (!canvas) return;
          setFillColor(color);
          canvas.backgroundColor = color;
          renderAll();
        }}
        onResetClick={() => {
          if (!canvas) return;
          setFillColor('');
          canvas.backgroundColor = undefined;
          renderAll();
        }}
      />
    </>
  );
};

export default GlobalControl;
