import React from 'react';
import { ColorResult, CustomPicker } from 'react-color';
import { EditableInput, Hue, Alpha, Saturation } from 'react-color/lib/components/common';
import { Pointer, SaturationPointer } from './ColorPickerPointers';

interface Style extends React.CSSProperties {}

type Styles = {
  [K in string]: Style | Styles;
};

interface Props {
  color: string;
  style: Style;
  placeholder: string;
  onChange: (color: ColorResult, event: React.ChangeEvent) => void;
  onInputChange: (color: ColorResult, event: React.ChangeEvent) => void;
  close: () => void;
}

function ColorPicker({
  color,
  style,
  placeholder,
  onChange,
  onInputChange,
  close,
  ...props
}: Props) {
  const styles: Styles = {
    editableInput: {
      input: {
        textAlign: 'center',
        width: '100%',
        border: 0,
        outline: 0,
        boxShadow: '#dadada 0px 0px 0px 1px inset',
        padding: '6px 0',
        fontSize: 12,
        marginBottom: 4,
        background: 'transparent',
      },
      label: {
        display: 'block',
        textAlign: 'center',
        fontSize: 11,
        textTransform: 'uppercase',
        color: '#969696',
      },
    },
    swatch: {
      background: color,
    },
  };

  const colorText = color === 'transparent' ? '' : color;

  return (
    <>
      <div className="picker-background" onClick={close}></div>
      <dialog className="picker" open style={style}>
        <section className="picker-saturation">
          <Saturation
            pointer={SaturationPointer}
            color={color}
            {...props}
            onChange={onChange}
          />
        </section>
        <section className="picker-detail">
          <div className="detail-top">
            <div className="detail-wrapper">
              <div className="detail-swatch" style={styles.swatch} />
            </div>
            <div className="detail-sliders">
              <div className="detail-hue">
                <Hue {...props} pointer={Pointer} onChange={onChange} />
              </div>
              <div className="detail-alpha">
                <Alpha pointer={Pointer} color={color} {...props} onChange={onChange} />
              </div>
            </div>
          </div>
          <div className="detail-bottom">
            {!colorText && <div className="detail-placeholder">{placeholder}</div>}
            <EditableInput
              label="Hex"
              style={
                styles.editableInput as Extract<React.ClassAttributes<EditableInput>, 'styles'>
              }
              value={colorText}
              onChange={onChange}
            />
          </div>
        </section>
      </dialog>
    </>
  );
}

export default CustomPicker(ColorPicker);
