import React, { useState } from 'react';
import { Circle, Gradient } from 'fabric/fabric-impl';
import GradientInputBox from './GradientInputBox';
import InputBox from './InputBox';

interface Props {
  item: Circle;
  renderAll: () => void;
}

const CircleControl = ({ item, renderAll }: Props) => {
  const { canvas } = item;

  const [radius, setRadius] = useState(item.radius);
  const [fillColor, setFillColor] = useState(item.fill as string | Gradient | undefined);
  const [startAngle, setStartAngle] = useState(360 - item.startAngle!);

  return (
    <>
      <InputBox
        label="반지름"
        className="controller-input"
        name="radius"
        type="range"
        value={radius}
        min={1}
        max={canvas!.height! / 2}
        onChange={(event: React.ChangeEvent) => {
          const value = Number((event.target as HTMLInputElement).value);
          item.set('radius', value);
          setRadius(value);
          renderAll();
        }}
      />
      <InputBox
        label="중심각"
        className="controller-input"
        name="startAngle"
        type="range"
        value={startAngle}
        min={0}
        max={360}
        onChange={(event: React.ChangeEvent) => {
          const value = Number((event.target as HTMLInputElement).value);
          item.set('startAngle', ((360 - value) * Math.PI) / 180);
          setStartAngle(value);
          renderAll();
        }}
      />
      <GradientInputBox
        label="채우기 색상"
        name="fillColor"
        color={fillColor}
        updateColor={(gradient) => {
          setFillColor(gradient);
          item.set('fill', gradient);
          renderAll();
        }}
        redraw={(gradient) => {
          item.set('fill', gradient);
          renderAll();
        }}
        onResetClick={() => {
          setFillColor('');
          item.set('fill', undefined);
          renderAll();
        }}
      />
    </>
  );
};

export default CircleControl;
