import React, { useState } from 'react';
import { fabric } from 'fabric';
import { Gradient } from 'fabric/fabric-impl';
import { convertToColorStyle, matchColorFormat, toGradient } from '../../utils/color';
import remove from '../../assets/svg/close_black_24dp.svg';
import GradientColorPicker from './GradientColorPicker';

interface Props {
  label?: string;
  className?: string;
  name?: string;
  color?: string | Gradient;
  updateColor: (gradient: Gradient) => void;
  redraw: (gradient: Gradient) => void;
  onResetClick: (event?: React.MouseEvent) => void;
}

function getColorText(fill?: string | Gradient): string {
  if (fill === 'transparent') {
    return '';
  } else if (fill instanceof fabric.Gradient) {
    const isSameColor =
      fill.colorStops &&
      fill.colorStops.every((item, i, arr) => {
        return i === 0 || item.color === arr[i - 1].color;
      });
    return isSameColor ? fill.colorStops![0].color : '그라데이션';
  }
  return fill || '';
}

const GradientInputBox = ({
  label,
  name,
  color,
  updateColor,
  redraw,
  onResetClick,
}: Props) => {
  const [isOpenPicker, setIsOpenPicker] = useState(false);
  const [pickerStyle, setPickerStyle] = useState<any>();
  const [value, setValue] = useState(getColorText(color));

  const id = Math.floor(Date.now() * Math.random() * Math.pow(10, 3)).toString(16);

  const gradientStyle = convertToColorStyle(color);

  function onInputChange(event: React.ChangeEvent) {
    const value = (event.target as HTMLInputElement).value.trim();
    setValue(value);

    const colorCode = matchColorFormat(value);
    if (colorCode) {
      const gradient = toGradient(colorCode, 0);
      updateColor(gradient);
    }
  }

  function onColorCircleClick(event: React.MouseEvent) {
    const { clientX, clientY } = event;
    const { height } = document.body.getBoundingClientRect();

    const y = Math.floor((clientY / height) * 100) < 80 ? -50 : -100;
    const pickerStyle = {
      transform: `translate(5%,${y}%)`,
      top: clientY,
      left: clientX,
    };

    setPickerStyle(pickerStyle);
    !isOpenPicker && setIsOpenPicker(true);
  }

  return (
    <div className="inputbox color">
      <label className="inputbox-label" htmlFor={id}>
        {label}
      </label>
      <div className="inputbox-wrapper">
        <input
          id={id}
          className="inputbox-input color"
          name={name}
          type="text"
          value={value}
          placeholder="색상 없음"
          onChange={onInputChange}
        />
        {value && (
          <button
            className="reset"
            onClick={() => {
              setValue('');
              onResetClick && onResetClick();
            }}
          >
            <img className="icon" src={remove} alt="되돌리기" />
          </button>
        )}
      </div>
      <div className="inputbox-sub color">
        <div
          className="inputbox-color"
          style={{ background: gradientStyle }}
          onClick={onColorCircleClick}
        ></div>
        {isOpenPicker && (
          <GradientColorPicker
            style={pickerStyle}
            fill={color}
            placeholder="색상 없음"
            updateGradient={(gradient) => {
              const colorText = getColorText(gradient);
              setValue(colorText);
              updateColor(gradient);
            }}
            redraw={redraw}
            close={() => setIsOpenPicker(false)}
          />
        )}
      </div>
    </div>
  );
};

export default GradientInputBox;
