import React, { useEffect, useRef, useState } from 'react';
import CanvasBG from './CanvasBG';
import GradientColorHandle from './GradientColorHandle';
import {
  convertToFabricGradient,
  convertToColorStyle,
  setBackground,
  limitGradientOffset,
} from '../../utils/color';
import { Gradient } from 'fabric/fabric-impl';
import { hideDragImage, removeDragImage } from '../../utils/drag-image';

interface Props {
  sliderRef?: React.MutableRefObject<HTMLElement>;
  pointer?: HTMLElement;
  value: Gradient;
  angle: number;
  updateGradient: (gradient: Gradient) => void;
  redraw: (gradient: Gradient) => void;
  changePointer: (pointer?: HTMLElement) => void;
}

const GradientSlider = ({
  sliderRef,
  pointer,
  value,
  angle,
  updateGradient,
  changePointer,
  redraw,
}: Props) => {
  const [width, setWidth] = useState<number>();

  const colorLineRef = useRef<HTMLDivElement>();

  let colorStops = [...value.colorStops!].map((item, i) => ({ ...item, id: String(i) }));

  let dragging: boolean = false;
  let draggingPointer: HTMLElement | undefined = pointer;
  let dragImage: HTMLElement | undefined;

  useEffect(() => {
    const colorLine = colorLineRef.current;
    if (!colorLine) return;

    const { width } = colorLine.getBoundingClientRect();
    setWidth(width - 16);
  }, []);

  function onColorDragStart(event: React.DragEvent) {
    dragging = true;
    draggingPointer = event.currentTarget as HTMLElement;
    dragImage = hideDragImage(event);
  }

  function onColorbarDragOver(event: React.DragEvent) {
    if (!width || !dragging || !draggingPointer) return;

    const { offsetX } = event.nativeEvent;
    const colorbar = event.nativeEvent.target as HTMLElement;

    requestAnimationFrame(() => {
      if (!draggingPointer) return;

      const position = offsetX > width ? width : offsetX;
      const offset = limitGradientOffset(position / width);

      draggingPointer.style.transform = `translateX(${position}px)`;

      colorStops = colorStops
        .map((color) => {
          return draggingPointer!.dataset.id === color.id ? { ...color, offset } : color;
        })
        .sort((a, b) => a.offset - b.offset);

      const gradient = convertToFabricGradient(colorStops, angle);
      const gradientStyle = convertToColorStyle(gradient);
      setBackground(colorbar, gradientStyle);

      redraw && redraw(gradient);
    });
  }

  function onColorDragEnd() {
    dragging = false;
    changePointer(draggingPointer);
    draggingPointer = undefined;

    const gradient = convertToFabricGradient(colorStops, angle);
    updateGradient && updateGradient(gradient);
    redraw && redraw(gradient);

    removeDragImage(dragImage);
  }

  function onColorbarClick(event: React.MouseEvent) {
    const { offsetX } = event.nativeEvent;
    const offset = limitGradientOffset(offsetX / width!);

    const updated = [...colorStops, { offset, color: '#ffffff' }];
    const sorted = updated.sort((a, b) => a.offset - b.offset);
    colorStops = sorted.map((item, i) => ({ ...item, id: String(i) }));

    const gradient = convertToFabricGradient(sorted, angle);

    updateGradient(gradient);
  }

  return (
    <div className="inputbox">
      <div className="inputbox-gradient">
        <div className="gradient">
          <div
            ref={colorLineRef as React.MutableRefObject<HTMLDivElement>}
            className="gradient-line"
          >
            <CanvasBG width={240} height={34} />
            <div
              ref={sliderRef as React.MutableRefObject<HTMLDivElement>}
              className="gradient-colorbar"
              style={{
                background: convertToColorStyle(colorStops),
              }}
              onClick={onColorbarClick}
              onDragOver={onColorbarDragOver}
            ></div>
          </div>
          <div className="gradient-colors">
            {value &&
              typeof value !== 'string' &&
              width &&
              value.colorStops &&
              Array.from(value.colorStops)?.map(({ offset, color }, i) => {
                const position = offset * width;

                return (
                  <GradientColorHandle
                    key={color + offset + i}
                    style={{ transform: `translateX(${position}px)`, background: color }}
                    id={String(i)}
                    color={color}
                    onDragStart={onColorDragStart}
                    onDragEnd={onColorDragEnd}
                    onClick={(event: React.MouseEvent) => {
                      changePointer(event.currentTarget as HTMLElement);
                    }}
                  />
                );
              })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default GradientSlider;
